main {
  grid-area: main;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #eee;
}

.elem-content {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  padding: 4rem;
}

.elem-main {
  position: relative;
  height: auto;
  min-height: 30rem;
  width: 92%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 4rem 2rem;
  text-align: center;
  border-radius: 3rem;
  overflow: hidden;
  padding: 2rem;
}
.elem-main:hover::before {
  filter: brightness(0.5);
}
.elem-main:hover > .main-text > .text-hidden {
  display: flex;
  transition: all 1s;
}
.elem-main:hover > .text-osteopathie {
  margin-bottom: 1rem;
}
.elem-main:hover > .main-text {
  margin-bottom: 2rem;
}
.elem-main:hover > .btn {
  backdrop-filter: none;
  text-decoration: underline;
}
@media (min-width: 480px) and (max-width: 1099px) {
  .elem-main {
    height: auto;
  }
}

